import Header from '@/components/Header'
import Footer from '@/components/Footer';
import TeamMember from '@/components/TeamMember';
import { teamMembers, advisoryMembers } from '@/data/teamData';

export default function WhoWeAre() {
  return (
    <>
      <Header />
      <main className="main">
        {/* Page Title */}
        <div className="page-title">
          <h1>Who We Are</h1>
        </div>

        {/* Growth Section */}
        <section className="growth-section">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 content-col">
                <h1 className="growth-title">Transforming Accessibility With Human-Centered AI</h1>
                <p className="growth-text">
                  At Signvrse, we use AI and inclusive design to break communication barriers for the Deaf community. 
                  By embedding real-time sign language access into digital platforms, we make education, healthcare,
                  and public services more inclusive. Our work supports long-term impact in line with the 
                  SDGs, ensuring no one is left behind in the digital age.
                </p>
              </div>
              <div className="col-lg-6">
                <div className="growth-image">
                  <img src="/assets/img/allteam.JPG" alt="Signvrse team" />
                </div>
              </div>
            </div>
          </div>
        </section>
   
        {/* Team Section */}
        <section id="team" className="team-section py-5">
          <div className="container" data-aos="fade-up">
            <div className="row mb-4 justify-content-center">
              <div className="col-lg-6 text-center">
                <h1 className="section-title">Our team</h1>
                <p className="section-description">
                  We're a purpose-driven team of Technologists, Deaf leaders and Creators building a more accessible world where everyone is included in the conversation.
                </p>
              </div>
            </div>
    
            <div className="row g-4 justify-content-center">
              {teamMembers.map((member, index) => (
                <TeamMember key={index} member={member} />
              ))}
            </div>
          </div>
        </section>

        {/* Advisory Section */}
        <section id="advisory" className="team-section py-5">
          <div className="container" data-aos="fade-up">
            <div className="row mb-4 justify-content-center">
              <div className="col-lg-6 text-center">
                <h1 className="section-title">Our Advisory</h1>
                <p className="section-description">
                  We're guided by experienced advisors who share our vision for inclusion and accessibility, 
                  offering strategic insight grounded in ethics, innovation, and lived experience.
                </p>
              </div>
            </div>
    
            <div className="row g-4 justify-content-center">
              {advisoryMembers.map((member, index) => (
                <TeamMember key={index} member={member} />
              ))}
            </div>
          </div>
        </section>

      </main>

      <Footer />
      <a href="#" id="scroll-top" className="scroll-top d-flex align-items-center justify-content-center"><i className="bi bi-arrow-up-short"></i></a>
    </>
  )
}
